import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import {
	View,
	Panel,
	Placeholder,
	PanelHeader,
	Button
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Icon56MessageOutline from '@vkontakte/icons/dist/56/message_outline';

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			activePanel: 'main'
		}
	}

	componentDidMount() {
		if(window.location.hash) {
			let url = window.location.hash.replace('#', '');
			window.location.href = url;
		}
	}

	render() {
		return (
			<View
				activePanel={this.state.activePanel}
			>
				<Panel
					id='main'
				>
					<PanelHeader>СМС из будущего</PanelHeader>
					<Placeholder
						icon={Icon56MessageOutline}
						header="Сообщения из будущего"
						action={
							<Button onClick={
								() => bridge.send('VKWebAppAllowMessagesFromGroup', {
									group_id: 199546900
								})
							} size="l">Включить СМС</Button>
						}
					>
						Разрешите получение сообщений от нас, чтобы мы могли Вам присылать СМС из будущего.
					</Placeholder>
				</Panel>
			</View>
		);
	}
}

export default App;

